.messages {
	display: flex;
	flex-direction: column;
}

.user {
	margin: 0;
	margin-right: 14px;
	margin-bottom: 7px;
	&Msg {
		background: rgb(149, 0, 255);
		color: white;
		border-radius: 10px;
		padding: 10px;
		margin-right: 10px;
	}
}

.other {
	margin: 0;
	margin-left: 14px;
	margin-bottom: 7px;
	&Msg {
		background: rgb(255, 131, 199);
		color: white;
		border-radius: 10px;
		padding: 10px;
		margin-left: 10px;
	}
}

.input {
	display: flex;
	position: fixed;
	bottom: 0;
	z-index: 999;
	width: 100%;
	padding-left: 13px;
	padding-right: 7px;
	padding-bottom: 8px;
	&Msg {
		border-radius: 50px;
		border: none;
		outline: none;
		padding: 10px;
	}
	&Btn {
		margin-left: 1em;
		color: white;
		border-radius: 50px;
		border: none;
		outline: none;
		cursor: pointer;
		background: purple;
	}
}

.modalInput {
	outline: none;
	border: none;
	border-radius: 8px;
	padding: 10px;
	background: rgb(240, 240, 240);
}

.modalBtn {
	background: purple;
	border-radius: 8px;
	padding: 10px 56px;
	border: none;
	transition: all .3s;
	color: white;
	&:disabled {
		background: gray;
	}
	&:hover {
		box-shadow: 0 0 10px rgb(130, 130, 130);
	}
	&:focus {
		outline: none;
	}
}
